import React from "react";
import { SearchPanel } from "./search-panel";
import { List} from "./list";
import { useDebounce, useDocumentTitle } from "../../utls";
import styled from "@emotion/styled";
import { useProjects } from "../../utls/project";
import { useUsers } from "../../utls/user";
import { useProjectModal, useProjectsSearchParams } from "./util";
import { ButtonNoPadding, ErrorBox, Row } from "../../components/lib";
import {Project} from "../../types/project";
export const ProjectListScreen = () => {
  useDocumentTitle("项目列表", false);
  //基本类型，可以放到依赖里；组件状态（state），可以放到依赖里，非组件状态的对象，绝不可以放到依赖里
  const [param, setParam] = useProjectsSearchParams();
  const { open } = useProjectModal();
  const { isLoading, error, data: projectList } = useProjects(
    useDebounce(param, 200)
  );
  const { data: users } = useUsers();

  const list = projectList
    ? projectList.map((item: Project) => ({ ...item, key: item.id }))
    : [];
  return (
    <Container>
      <Row between={true}>
        <h1>项目列表</h1>
        <ButtonNoPadding type={"link"} onClick={open}>
          创建项目
        </ButtonNoPadding>
      </Row>
      <SearchPanel users={users || []} param={param} setParam={setParam} />
      <ErrorBox error={error} />
      <List loading={isLoading} users={users || []} dataSource={list} />
    </Container>
  );
};
// ProjectListScreen.whyDidYouRender = false;

const Container = styled.div`
  width: 100%;
  padding: 3.2rem;
`;
