import { useEffect, useRef, useState } from "react";

export const isFalsy = (value: unknown): boolean =>
  value === 0 ? false : !value;

export const isVoid = (value: unknown) =>
  value === undefined || value === null || value === "";

export const cleanObject = (object: { [key: string]: unknown }) => {
  const result = { ...object };
  Object.keys(result).forEach((key) => {
    const value = result[key];
    if (isVoid(value)) {
      delete result[key];
    }
  });
  return result;
};

export const useMount = (callback: () => void) => {
  useEffect(() => {
    callback();
    //todo 依赖黎加上callback会造成无限循环，这个和useCallback以及useMemo有关系
    //eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
};

export const useDebounce = <V>(value: V, delay?: number) => {
  const [debouncedValue, setDebouncedValue] = useState(value);
  useEffect(() => {
    const timeout = setTimeout(() => {
      setDebouncedValue(value);
    }, delay);
    return () => clearTimeout(timeout);
  }, [value, delay]);
  return debouncedValue;
};
export const useArray = <T>(values: T[]) => {
  const [value, setValue] = useState(values);
  const clear = setValue([]);
  const removeIndex = (idx: number) => {
    const restValue = [...value].filter(
      (item: unknown, index: number) => index !== idx
    );
    setValue(restValue);
  };
  const add = (val: T) => {
    const addValue = { ...value, val };
    setValue(addValue);
  };

  return { value, clear, removeIndex, add };
};
export const useDocumentTitle = (title: string, keepOnUnmount = true) => {
  const oldTitle = useRef(document.title).current;
  useEffect(() => {
    document.title = title;
  }, [title]);
  useEffect(() => {
    return () => {
      if (!keepOnUnmount) {
        document.title = oldTitle;
      }
    };
    //eslint-disable-next-line react-hooks/exhaustive-deps
  }, [keepOnUnmount, oldTitle]);
};
export const resetRoute = () => (window.location.href = window.location.origin);

export const subSet = <
  O extends { [key in string]: unknown },
  K extends keyof O
>(
  obj: O,
  keys: K[]
) => {
  const filteredEntries = Object.entries(obj).filter(([key]) =>
    keys.includes(key as K)
  );
  return Object.fromEntries(filteredEntries) as Pick<O, K>;
};
/**
* 返回组件的挂载状态，如果还没挂载或已经卸载，返回false，反之返回true
* */
export const useMountedRef = () =>{
  const mountedRef = useRef(false)

  useEffect(()=>{
    mountedRef.current = true
    return ()=>{
      mountedRef.current = false
    }
  })
  return mountedRef
}
